import React, { Component } from 'react';
import '../css/Skills.css';

import SkillItem from './SkillItem';
import BackEnd from '../assets/images/backend.svg';
import Tools from '../assets/images/tools.svg';
import FrontEnd from '../assets/images/frontend.svg';

const skillsContents = [
  {
    layer: 'Back-end',
    skill: ['Python Tornado', 'Java Spring', 'Node.js/ Express', 'MySQL', 'MongoDB',],
    logo: BackEnd
  }, 
  {
    layer: 'Front-end',
    skill: ['React', 'Angular', 'Vue', 'JavaScript'],
    logo: FrontEnd
  },

  {
    layer: 'Tools',
    skill: ['Git', 'AWS', 'Linux', 'Docker', 'Kubernetes'],
    logo: Tools
  }
];


class Skills extends Component {
  render() {
    return (
      <div className="skills">
        <h1>Skills</h1>
        <div className="skills-content">
          {this.renderSkills()}
        </div>
      </div>
    );
  }

  renderSkills() {
    return skillsContents.map((item, i) => {
      return (
        <SkillItem key={i} {...item} />
      )
    });
  }
}

export default Skills;