import React, { Component } from 'react';
import '../css/Experience.css';

import ExperienceItem from './ExperienceItem';
import Arista from '../assets/images/arista.png';
import Nielsen from '../assets/images/nielsen.png';
import Wish from '../assets/images/wish.png';
import EzMHP from '../assets/images/EzMHP.png';

const experienceContents = [

  {
    company: "Arista Networks",
    position: "Software Engineer Coop",
    date: "Incoming Sep 2021",
    smallUrl: Arista,
    imageUrl: Arista,
    description: [
      "Software Engineer at CloudVision team"
    ]
  },
  {
    company: "Wish",
    position: "Software Engineer Intern",
    date: "April 2021 - Present",
    smallUrl: Wish,
    imageUrl: Wish,
    description: [
      "Working as a backend developer at Wish marketplace team, tackling logistics-related issues in python",
      "Built a cam monitoring module with custom websocket subprotocol to capture live stream from a third-party application for inventory verification, reverting from binary message and upload videos to AWS S3",
      "Optimized a mixed-size packaging algorithm to reduce domestic logistics costs by an average of 1.33%",

    ]
  },

  {
    company: "EzMHP.com",
    position: "Software Development Engineer Intern",
    date: "Oct 2020 - Mar 2021",
    smallUrl: EzMHP,
    imageUrl: EzMHP,
    description: [
      "Maintained a website deployed on Amazon AWS EC2 and RDS for a mobile home park management company located in Vancouver, BC, with business covering 3 states in U.S. for 1800+ tenants",
      "Modified scripts for automating 16000+ monthly billings to tenants and bi-weekly report to mobile home park managers in both python and JavaScript",
      "Marked tenants by a 1:5:1 scale based on late payments, default rates in mssql for administrative needs",
      "Refactored the v2.0 backend urls to comply with the RESTful design principles"
      
    ]
  },
  {
    company: "Nielsen",
    position: "Marketing Data Analyst Intern",
    date: "Dec 2018 - May 2019",
    smallUrl: Nielsen,
    imageUrl: Nielsen,
    description: [
      "Managed marketing data extraction and maintenance with MySQL for a vehicle lubricant market leader; efforts contributed to more than 65% of data preparation and ad-hoc analysis including outlier identification and parameter adjustment for client service.",
    ]
  },
]

class Experience extends Component {
  render() {
    return (
      <div className="experience">
        <h1>Professional Experience</h1>
        {this.renderExperience()}
      </div>
    );
  }

  renderExperience() {
    return experienceContents.map((item) => {
      return (
        <ExperienceItem key={item.company} {...item} />
      )
    });
  }
}

export default Experience;
