import React, { Component } from 'react';
import '../css/Banner.css';
import TypeWriter from './TypeWriter';
import Portrait from '../assets/images/portrait.png';
import Type from './Type'

class Banner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      nextLine: false
    };
    this.signalTextFinish = this.signalTextFinish.bind(this);
  }

  signalTextFinish() {
    this.setState({ nextLine: true });
  }

  render() {
    return (
      <div className="banner">
        <div className="image-links">
          <img className="portrait" src={Portrait} alt="portrait" />
          {/* <Links /> */}
          
        </div>
        <div className="name">
        <p>
        Jerome Ju <br></br>
        <Type />
        </p>
        <div className="typed-text">
          <TypeWriter
            text="A second-degree CS student with a previous business background"
            onFinish={this.signalTextFinish}
            isFirst={true}
          />
          {
            this.state.nextLine ?
              <TypeWriter text="Seeking fulltime opportunities as an software engineer." />
              :
              <div className="space-holder" />
          }
        </div>
        </div>
        <div className="description">

        </div>
      </div>
    );
  }
}

export default Banner;